import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	navBar,
	appid,
	appsecret
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			identifying: '',
			count:0
		}
	},
	methods: {
		leftClick() {




			uni.navigateBack({
				delta: 1
			});

		},
		pageUrl(url) {
			this.count++;
			if (this.count != 1) return;
			setTimeout(() => {
				this.count = 0;
			}, 1000)
			var that = this;
			var data = that.identifying;
			uni.navigateTo({
				url: url+'?identifying=' + data
			});
		}
	},
	onLoad(options) {
		var that = this;
		that.identifying = options.identifying;uni.setNavigationBarTitle({
			    title: uni.getStorageSync('titleKey')
			});
	}
}
